import axios from 'axios'

const domain = 'http://localhost:5000'
const api = 'api'
const url = `${domain}/${api}`
export const authTokenName = 'AUTH_TOKEN'
export const authToken = localStorage.getItem(authTokenName)

const paths = {
  auth: {
    signIn: `${url}/auth/signin`,
    signUp: `${url}/auth/signup`,
    checkToken: `${url}/auth/checkToken`
  },
  user: {
    getUser: `${url}/user`
  },
  post: {
    getAll: `${url}/post/getAll`,
    getOne: `${url}/post/getOne`,
    getByUserID: `${url}/post/getById`,
    create: `${url}/post/create`
  },
  comment: {
    getAll: `${url}/comment/getByPostId`,
    create: `${url}/comment/create`
  }
}

export const requestGet = (url, isAuth) => {
  return axios.get(url, {
    headers: {
      authorization: isAuth ? authToken : null
    }
  })
}

export const requestPost = (url, params, isAuth) => {
  return axios.post(url, params, {
    headers: {
      authorization: isAuth ? authToken : null
    }
  })
}

export default paths

